.content{	
	order: 2;
    -webkit-order: 2;
}
.btn{
	width: 280px;
	height: 55px;
	border-radius: 40px;
	font: 400 18px $proxi;
	color: #fff;
	border: 0;
	background: #2c14a2;
	transform: perspective(1px) translateZ(0);
	position: relative;
	transition: all 0.3s;
	overflow: hidden;
	&:before{
		content: "";
	    position: absolute;
	    z-index: -1;
	    top: 0;
	    left: 0;
	    right: 0;
	    bottom: 0;
	    background: #181faf;
	    transform: scaleY(0);
	    transform-origin: 50% 100%;
	    transition: all 0.3s ease-out;
	    border-radius: 40px;
	}
	&:hover{
		&:before{
			transform: scaleY(1);
		}
	}
	&_2{
		background: #fff;
		box-shadow: 4px 4px 10.68px 1.32px rgba(77, 85, 231, 0.33);
		overflow: visible;
		color: #232c3c;
		font-size: 16px;
		height: 40px;
		width: 162px;
		&:before{
			content: none;
		}
		&:hover, &:active{
			color: #526ad2;
		}
	}
}
.flex{
	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-wrap: wrap;
}
.banner{
	background-color: #1a6682;
	/*background-image: url(../img/banner.jpg);*/
	background-position: center center;
	background-size: cover;
	position: relative;
	overflow: hidden;
	z-index: 2;
	.wrapper{
		height: 100vh;
		min-height: 600px;
		&.flex{
			justify-content: center;
		}
	}
	&__image{
		position: absolute;
    	right: -2%;
    	top: -2%;
    	z-index: 1;
    	height: 105%;
	}
	&__body{
		text-align: center;
		position: relative;
		z-index: 2;
		h1{
			font: 400 24px $rubik;
			color: #38aed6;
			letter-spacing: 0.02em;
			margin-bottom: 20px;
		}
		h2{
			font: 700 50px $proxi;
			text-transform: uppercase;
			color: #fff;
			letter-spacing: 0.02em;
			margin-bottom: 40px;
		}
		&:hover{
			.banner{
				&__logo{
					transform: translateX(20px);
				}
			}
		}
	}
	&__logo{
		position: absolute;
		left: 50%;
		top: 50%;
		width: 870px;
		height: 420px;
		margin-left: -430px;
		margin-top: -230px;
		z-index: -1;
		transition: all 0.3s ease-out;
		&:hover{
			transform: translateX(20px);
		}
	}
	&__scroll{
		position: absolute;
		left: 50%;
		margin-left: -19px;
		bottom: 10%;
		width: 38px;
		height: 58px;
		border-radius: 20px;
		/*box-shadow: 0 0 0 2px rgba(255,255,255,0.3);*/
		border: 2px solid #fff;
		opacity: 0.3;
		z-index: 5;
		transition: all 0.3s linear;
		span{
			display: block;
			width: 3px;
			position: absolute;
			left: 50%;
			margin: auto;
			margin-left: -1px;
			top: 12px;
			bottom: 28px;
			background: #fff;
			animation: banner 2s infinite linear;
		}
		&:hover{
			opacity: 1;
		}
	}
}
@keyframes banner{
	0%{
		top: 12px;
		bottom: 28px;
	}
	12.5%{
		top: 12px;
		bottom: 28px;
	}
	25%{
		bottom: 12px;
	}
	37.5%{
		top: 28px;
	}
	50%{
		top: 28px;
	}
	62.5%{
		top: 28px;
	}
	75%{
		bottom: 12px;
	}
	87.5%{
		top: 12px;
		bottom: 28px;
	}
	100%{
		top: 12px;
		bottom: 28px;
	}
}
