				opacity: 1;
			}
		}
		.slick-list{
			margin: 0 -50px;
			transform: translate3d(0, 0, 0);
			overflow: visible;
			z-index: 2;
		}
		.slick-prev, .slick-next{
			text-indent: 9999px;
			width: 9px;
			height: 20px;
			position: absolute;
			bottom: -50px;
			left: 50%;
			background: url(../img/prev.png);
			background-size: 100% 100%;
			cursor: pointer;
			border: 0;
			z-index: 1;
		}
		.slick-prev{
			margin-left: -55px;
		}
		.slick-next{
			margin-right: -55px;
			transform: rotate(180deg);
		}
	}
	&__item{
