		right: 11px;
    	top: 5px;
		width: 22px;
		animation: hovericon 1s infinite ease-in-out;
	}
	&__more2{
		position: absolute;
		right: 0;
    	top: 30px;
    	width: 30px;
    	animation: hovericon 1s infinite ease-in-out;
	}
	&__title{
		font: 700 20px $proxi;
		color: #232c3c;
		display: inline-block;
		position: relative;
		margin-bottom: 35px;
		&:before{
			content: "«";
			color: #39aed5;
			right: 100%;
			margin-right: 5px;
		}
		&:after{
			content: "»";
			color: #39aed5;
			left: 100%;
			margin-left: 5px;
		}
	}
	&__list{
		display: none;
		padding: 0 35px;
    	margin-bottom: 30px;
    	list-style: none;
    	position: relative;
    	li{
    		display: block;
    		height: 24px;
		    line-height: 24px;
		    overflow: hidden;
		    span{
		    	float: left;
		    	font: 400 13px $proxi;
		    }
		    b{
		    	float: right;
		    	font: 700 13px $proxi;
		    	small{
		    		font-size: 11px;
		    	}
		    }
    	}
	}
	&__desc{
		position: relative;
		&.flex{
			justify-content: center;
		}
		img{
			margin-right: 10px;
		}
		p{
			font: 400 18px $roomm;
			color: #5b6c86;
		}
	}
	&__per{
		width: 81px;
		height: 81px;
