	}
	&__tabs{
		margin-bottom: 40px;
	}
	&__tab{
		display: inline-block;
		padding: 18px 45px;
		cursor: pointer;
		span{
			font: 400 18px $roomm;
			color: #408c9f;
			border-bottom: 2px solid #408c9f;
			padding-bottom: 3px;
		}
		&.active{
			background: #408c9f;
			border-radius: 50px;
			border-top-left-radius: 0;
			span{
				font: 500 24px $roomm;
				color: #fff;
				border: 0;
				padding: 0;
			}
		}
	}
	&__form{
		padding: 0 40px;
	}
	&__input{
		width: 100%;
		height: 40px;
		border-radius: 20px;
		border: 1px solid #eaeaea;
		margin-bottom: 20px;
		position: relative;
		&.flex{
			justify-content: flex-start;
		}
		&.active{
			border-color: #09b1f0;
			.modal{
				&__label{
					opacity: 1;
				}
			}
		}
		input{
			height: 20px;
			width: calc(100% - 35px);
			border: 0;
			background: #fff;
			font: 400 16px $proxi;
			color: #232c3c;
			padding: 0 15px;
			border-left: 2px solid #dfdfdf;
			&::placeholder{
				font: 400 16px $proxi;
				color: #9ea2a9;
			}
		}
		&_2{
			width: 235px;
			height: 40px;
			border-radius: 10px;
			margin-right: 30px;
			margin-bottom: 0;
			.modal{
				&__label{
					opacity: 1;
					left: 10px;
					right: auto;
				}
			}
			input{
				height: 100%;
				border: 0;
				border-radius: 10px;
			}
		}
	}
	&__label{
		position: absolute;
		background: #fff;
		padding: 5px 10px;
		top: -17px;
    	right: 11px;
    	opacity: 0;
    	transition: all 0.3s;
		span{
			font: 400 14px $proxi;
			color: #9ea2a9;
		}
	}
	&__icon{
		width: 33px;
		padding-left: 3px;
		&.flex{
			justify-content: center;
		}
	}
	&__placeholder{
		font: 400 16px $proxi;
		color: #9ea2a9;
		margin-bottom: 25px;
	}
	&__check{
		margin-bottom: 50px;
		input{
			display: none;
		}
		label{
			font: 400 16px $proxi;
			color: #9ea2a9;
			position: relative;
			padding-left: 40px;
			cursor: pointer;
			display: block;
			&:before{
				content: "";
				width: 20px;
				height: 20px;
				background: #fff;
				border-radius: 3px;
				border: 1px solid #408c9f;
				position: absolute;
				left: 0;
				top: 50%;
				margin-top: -10px;
				transition: all 0.3s ease-out;
			}
			&:after{
				content: "";
				width: 10px;
				height: 7px;
				background: url(../img/check2.png);
				background-size: 100% 100%;
				position: absolute;
				left: 5px;
				top: 50%;
				margin-top: -2.5px;
				opacity: 0;
				transition: all 0.3s ease-out;
			}
			a{
				color: #408c9f;
			}
		}
		input:checked + label:before{
			background: #408c9f;
		}
		input:checked + label:after{
			opacity: 1;
		}
		&_2{
			label{
				&:before{
					top: 0;
					margin-top: 0;
				}
				&:after{
					top: 0;
					margin-top: 7px;
				}
			}
		}
	}
	&__kapcha{
		margin-bottom: 30px;
		&.flex{
			justify-content: flex-start;
		}
	}
	&__code{
		font: 400 24px $proxi;
		color: #63c694;
	}
	&__bts{
		&.flex{
			justify-content: flex-start;
		}
		.btn{
			width: 235px;
			height: 50px;
			margin-right: 35px;
		}
	}
	&__forget{
		font: 400 16px $proxi;
		color: #232c3c;
	}
	&__rows{
		width: 100%;
	}
	&__row{
		.modal{
			&__input{
				width: 270px;
			}
		}
	}
	&__col{
		width: 270px;
	}
}
.overlay{
	display: none;
	background: #363636;
	position: fixed;
	top: 0;
	left: 0;
  	width: 100%;
  	height: 100%;
  	opacity: 0.65;
  	-moz-opacity:0.65; 
  	filter:alpha(opacity=65);
	z-index: 9998;
	overflow: hidden;
}




@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
    body{min-height: 100hv; width: 100%; display: flex; flex-direction: column;}
}
@media screen and (min-width: 992px) and (max-width: 1200px) {
	.wrapper{
		max-width: 900px;
	}
	.abouthome{
		&__infobox{
			position: relative;
    		z-index: 5;
		}
	}
	.calc{
		&__left{
			width: 410px;
		}
		&__right{
