
/*
================================================================================
|                                     HEADER                                   |
================================================================================
*/
header{
	order: 1;	
    -webkit-order: 1;
}
.header{
	width: 100%;
	background: none;
	position: absolute;
	top: 0;
	left: 0;
	padding: 27px 0;
	z-index: 100;
	&__nav{
		ul{
			list-style: none;
			&.flex{
				justify-content: center;
			}
			li{
				margin: 0 24px;
				position: relative;
				&:before{
					content: "";
				    position: absolute;
				    z-index: 0;
				    height: 40px;
				    top: -43px;
				    bottom: 0;
				    left: 0;
				    right: 0;
				    background: url("../img/hover-menu.png") no-repeat;
				    background-size: 100% 100%;
				    transform: scaleX(0);
				    transform-origin: 50%;
				    transition: all 0.3s ease-in-out;
				}
				a{
					font: 500 16px $roomm;
					color: #fff;
					text-transform: uppercase;
					letter-spacing: 0.06em;
				}
				&:hover{
					&:before{
						transform: scaleX(1);
					}
					a{
						color: #19d8c8;
					}
				}
			}
		}
	}
	&__logs{
		padding: 12px 25px;
		border: 1px solid #fff;
		border-radius: 30px;
		box-shadow: 0 0 0 999px rgba(255,255,255,0.1) inset;
	}
	&__logitem{
		a{
			font: 400 16px $proxi;
			color: #fff;
			cursor: pointer;
			margin-bottom: -2px;
			&:hover{
				color: #48c5ee;
			}
		}
	}
	&__sign{
		margin-right: 24px;
		margin-left: 17px;
		position: relative;
		&:after{
			content: "";
			position: absolute;
			width: 15px;
			height: 44px;
			background: url(../img/sep.png) no-repeat;
			background-size: 100% 100%;
			right: -20px;
    		top: -12px;
		}
	}
	&__toggle{
		display: none;
		width: 30px;
		position: absolute;
		right: 35px;
    	top: 11px;
		span{
			display: block;
			width: 100%;
			height: 3px;
			margin-bottom: 5px;
			background: #fff;
			&:last-child{
				margin-bottom: 0;
			}
		}
	}
}
.languages{
	position: absolute;
	right: 0;
	top: 34px;
	&__selected{
		position: relative;
		&:after{
			position: absolute;
		    content: "";
		    top: 17px;
		    cursor: pointer;
		    right: 9px;
		    width: 0;
		    height: 0;
		    border: 6px solid transparent;
		    border-top-width: 7px;
		    border-color: #111f26 transparent transparent transparent;
		}
	}
	&__list{
		display: none;
	}
	&__item{
		width: 90px;
		height: 36px;
		background: #fff;
		border-radius: 2px;
		padding: 0 8px;
		margin-bottom: 1px;
		cursor: pointer;
		transition: all 0.3s ease-in-out;
		&.flex{
			justify-content: flex-start;
		}
		span{
			display: block;
			margin-left: 12px;
		}
		&:hover{
			background: #e9eff2;
		}
		&.same{
			height: 39px;
			&:hover{
				background: #fff;
			}
		}
	}
}

@media screen and (min-width: 1201px) and (max-width: 1400px) {
	.header{
		.wrapper{
			max-width: 1000px;
		}
	}
	.languages{
		&__selected{
			&:after{
				right: 6px;
			}
		}
		&__item{
			padding: 0 6px;
			width: 80px;
			span{
				margin-left: 8px;
			}
		}
	}
}

@media screen and (max-width: 1200px) {
	.header{
		&__logo{
			img{
				width: 80px;
			}
		}
	}
	.languages{
		top: 30px;
		&__selected{
			&:after{
				content: none;
			}
		}
		&__item{
			padding: 0 6px;
			width: 62px;
			span{
				margin-left: 8px;
			}
		}
	}
}

@media screen and (min-width: 992px) and (max-width: 1200px) {
	.header{
		.wrapper{
			max-width: 850px;
		}
		&__nav{
			ul{
				li{
					margin: 0 10px;
					&:first-child{
						margin-left: 0;
					}
					&:last-child{
						margin-right: 0;
					}
				}
			}
		}
		&__logs{
			padding: 12px 15px;
		}
	}
}

@media screen and (min-width: 768px) and (max-width: 991px) {
	.header{
		.wrapper{
			max-width: 690px;
		}
		&__logo{
			img{
				width: 65px;
			}
		}
		&__nav{
			ul{
				li{
					margin: 0 8px;
					&:first-child{
						margin-left: 0;
					}
					&:last-child{
						margin-right: 0;
					}
					a{
						font-size: 13px;
					}
				}
			}
		}
		&__logs{
			padding: 8px 10px;
    		overflow: hidden;
		}
		&__sign{
			margin-right: 20px;
    		margin-left: 12px;
		}
		&__logitem{
			a{
				font-size: 14px;
			}
		}
	}
	.languages{
		top: 28px;
		&__item{
			width: 35px;
			height: 34px;
			&.same{
				height: 34px;
			}
			span{
				display: none;
			}
		}
	}
}

@media screen and (max-width: 767px) {
	.header{
		padding: 15px 0;
		transition: all 0.3s ease-in-out;
		&__nav{
			margin-bottom: 15px;
			ul{
				li{
					display: block;
					width: 100%;
					text-align: center;
					margin: 5px 0;
					&:before{
						content: none;
						}				}
			}
		}
		&__logs{
			width: 200px;
			padding: 8px 10px;
    		overflow: hidden;
    		margin: 15px auto 0;
		}
		&__sign{
			margin-right: 20px;
    		margin-left: 12px;
		}
		&__logitem{
			a{
				font-size: 14px;
			}
		}
		&__toggle{
			display: block;
		}
		&__box{
			display: none;
		}
		&.active{
			background: #232c3c;
		}
	}
	.languages{
		top: 18px;
		&__item{
			width: 35px;
			height: 34px;
			&.same{
				height: 34px;
			}
			span{
				display: none;
			}
		}
	}
}
