
/*
================================================================================
|                                     FOOTER                                   |
================================================================================
*/
footer{
	order: 3;	
    -webkit-order: 3;	
}
.footer{
	padding: 170px 0 30px;
	background: url(../img/footer-bg.jpg) center center no-repeat;
	background-size: auto 100%;
	&__banner{
		width: 100%;
		border: 1px solid #fff;
		padding: 45px 70px 40px 60px;
		margin-bottom: 45px;
		.btn{
			width: 235px;
			height: 45px;
			font-size: 18px;
		}
	}
	&__info{
		width: 505px;
		position: relative;
		p{
			font: 700 24px $roboto;
			color: #fff;
			span{
				font-weight: 400;
				color: #38aed6;
				display: block;
			}
		}
		&:before{
			content: "";
			width: 152px;
			height: 58px;
			position: absolute;
			background: url(../img/arrow.svg);
			background-size: 100% 100%;
			top: 50%;
			margin-top: -29px;
			right: -205px;
		}
	}
	&__soc{
		margin-bottom: 45px;
		&.flex{
			justify-content: center;
		}
		a{
			display: block;
			margin: 0 13px;
			transition: all 0.3s;
			&:hover{
				transform: scale(1.2);
			}
		}
	}
	&__nav{
		margin-bottom: 45px;
		ul{
			li{
				&:before{
					height: 38px;
					top: -40px;
					background: url("../img/hover-menu-2.png") no-repeat;
				    background-size: 100% 100%;
				}
			}
		}
	}
	&__logo{
		display: block;
		text-align: center;
		margin-bottom: 40px;
	}
	&__copy{
		text-align: center;
		color: #cad8e4;
		font: 100 14px $proxi;
	}
}


@media screen and (min-width: 992px) and (max-width: 1200px){
	.footer{
		&__banner{
			padding: 45px 30px 40px 30px;
		}
		&__info{
			&:before{
				width: 100px;
			    height: 50px;
			    margin-top: -25px;
			    right: -87px;
			}
		}
	}
}

@media screen and (min-width: 768px) and (max-width: 991px) {
	.footer{
		&__banner{
			padding: 45px 30px 40px 30px;
		}
		&__info{
			width: 390px;
			&:before{
				content: none;
			}
		}
	}
}

@media screen and (max-width: 767px) {
	.footer{
		background: url(../img/footer-mob.jpg) center center no-repeat;
		background-size: cover;
		padding: 50px 0 30px;
		&__info{
			margin-bottom: 15px;
			&:before{
				content: none;
			}
			p{
				font-size: 17px;
			}
		}
		&__banner{
			margin-bottom: 25px;
			padding: 25px;
		}
		&__soc{
			margin-bottom: 20px;
		}
		&__nav{
			margin-bottom: 20px;
		}
		&__logo{
			margin-bottom: 20px;
		}
	}
}